	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Dive Into Paint Cans","date":"05 Aug 2002","slug":"/2002-08-05-dive-into-paint-cans/","tags":"Y2002"},"html":"<p><a href=\"http://www.wdrmaus.de/spielen/mausspiele/?lang=en\"><img alt=\"mausgame.gif\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/08/mausgame.gif\" width=\"150\" height=\"116\" border=\"0\" align=\"right\" /></a>For some reason, the <a href=\"http://diveintomark.org/archives/2002/08/02.html#css_solutions\">new rollover logo</a> at <a href=\"http://diveintomark.org\">diveintomark</a> reminds of  <a href=\"http://www.wdrmaus.de/spielen/mausspiele/?lang=en\">the paintcan cartoon game</a> (the orange and blue graphic at the upper left, Flash required) at the website of the German children's show 'Sendung mit der Maus'. Maybe because Christopher likes the Maus games, and insists on seeing them all the time. Hint: click on the blue can for the elephant, and on the orange can for the mouse.</p>"},"next":{"fields":{"title":"The Gentleman Upstairs","date":"04 Aug 2002","slug":"/2002-08-04-the-gentleman-upstairs/"}},"previous":{"fields":{"title":"Night Out","date":"05 Aug 2002","slug":"/2002-08-05-night-out/"}}},{"node":{"fields":{"title":"The Gentleman Upstairs","date":"04 Aug 2002","slug":"/2002-08-04-the-gentleman-upstairs/","tags":"Y2002"},"html":"<p><a href=\"http://www.dangerousmeta.com/posts/02/20020803\">dangerousmeta!</a>: \"she said, knowing how dry we've been, that she'd 'talk to the gentleman upstairs' and send us some rain. looks like she did\" Well done.</p>"},"next":{"fields":{"title":"In-Laws to Royalty","date":"04 Aug 2002","slug":"/2002-08-04-in-laws-to-royalty/"}},"previous":{"fields":{"title":"Dive Into Paint Cans","date":"05 Aug 2002","slug":"/2002-08-05-dive-into-paint-cans/"}}},{"node":{"fields":{"title":"In-Laws to Royalty","date":"04 Aug 2002","slug":"/2002-08-04-in-laws-to-royalty/","tags":"Y2002"},"html":"<p>My parents are finalizing their plans for a tour through Scandinavia, with a Christopher visits both before and afterwards. While telephoning this evening, my father told me a family story that I hadn't heard before... that a cousin(?) of my grandmother was married to a sibling(?) of King Olav of Norway. Or so says my great uncle, who says the cousin accompanied the King on one of his visits to Minneapolis, and called him in rural Minnesota to ask him to come visit. My great uncle had to decline, as he no longer drove and could not get to Minneapolis (and didn't think to ask one of his nephews to drive for him). Details are hard to confirm, as my great uncle is now both elderly and very hard of hearing. At any rate, I will not be knocking on the door of Crown Prince Haakon and Princess Mette-Marit, claiming to be a long-lost in-law.</p>"},"next":{"fields":{"title":"Christopher Exhausted","date":"04 Aug 2002","slug":"/2002-08-04-christopher-exhausted/"}},"previous":{"fields":{"title":"The Gentleman Upstairs","date":"04 Aug 2002","slug":"/2002-08-04-the-gentleman-upstairs/"}}}],"pathPrefix":"","first":false,"last":false,"index":863,"pageCount":1101}}

